.section {
  &__models {
    &-row {
      &:first-of-type {
        max-width: 78px;
      }

      &:nth-of-type(2) {
        max-width: 671px;
        margin: 0 auto 48px;
      }

      .card {
        border-radius: 15px;

        img {
          border-radius: 15px;

        }

        &-title {
          text-align: left;
          margin-bottom: 10px;
          @include fonts(600, 20px, 26px, $color-white);

        }

        &-text {

          @include fonts(400, 14px, 20px, $color-white);

        }

        &-img-overlay {
          height: fit-content;
          top: unset;
          padding: 22px 24px;
        }

        &:hover {

          svg {
            transform: translateX(10px);
            transition: .3s all ease-in-out;

          }
        }

        .learn-more {
          margin-top: 5px;
          text-align: end;
          column-gap: 16px;
          display: flex;
          align-items: center;

          &-divider {
            display: block;
            height: 1px;
            background: rgba($color-white, 0.15);
            width: 100%;
          }

          div {
            max-width: fit-content;
            width: 100%;
          }

          &-text {
            @include fonts(500, 14px, 20px, $color-white);
            margin-right: 8px;
          }
          svg {
            transition: .3s all ease-in-out;
          }
        }
      }
    }
  }
}